  /**
   * Metodo que permite seleccionar la imagen de perfil y actualizar la informacion de actualizacion hacia la base de datos.
   */
  // tslint:disable-next-line:typedef
  selectPhoto(datos) {
    let idFotos =
      typeof datos != "object" ? datos : datos.getAttribute("data-idphoto");
    let type =
      typeof datos != "object" ? datos : datos.getAttribute("data-type");

    let info = {
      idFotosperfil: idFotos,
      idMembers: this.user.idMembers,
      tipo: type,
    };

    // console.log(info);

    this.empresaService.postCambiarFotoPerfil(info).subscribe(() => {});
    this.userService
      .getInfoUser({
        idMembers: this.user.idMembers,
        tipoUser: this.user.tipoCuenta,
      })
      .subscribe((res) => {
        this.settingsService.actualizarInfo.emit(true);
        location.reload();
      });
  }
  /**
   * Metodo que permite eliminar la imagen de perfil y actualizar la informacion de actualizacion hacia la base de datos.
   */
  // tslint:disable-next-line:typedef
  deletePhoto(datos) {
    let idFotos =
      typeof datos != "object" ? datos : datos.getAttribute("data-idphoto");
    let type =
      typeof datos != "object" ? datos : datos.getAttribute("data-type");
    let fav = typeof datos != "object" ? datos : datos.getAttribute("data-fav");
    let path =
      typeof datos != "object" ? datos : datos.getAttribute("data-path");

    let info = {
      idFotosperfil: idFotos,
      idMembers: this.user.idMembers,
      tipo: type,
      favorita: fav,
      path: path,
    };
    if (
      path == "avatarundefined.png" ||
      path == "medico.png" ||
      path == "portada_medico.png" ||
      path == "background_default.png" ||
      path == "medical-2008481_640.png"
    ) {
      this.appService.swal(
        "Advertencia",
        "No puedes eliminar las imagenes por defecto, por favor añade fotos nuevas.",
        "warning"
      );
    } else this.empresaService.postEliminarFotoPerfil(info).subscribe(() => {});
    this.userService
      .getInfoUser({
        idMembers: this.user.idMembers,
        tipoUser: this.user.tipoCuenta,
      })
      .subscribe((res) => {
        this.settingsService.actualizarInfo.emit(true);
        location.reload();
      });
  }

  /**
   * Metodos para seleccionar municipio en el HTML segun el rol
   * @param municipio
   */
