import { Injectable, EventEmitter } from '@angular/core';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class SettingsService {

  public mostrarMenu: EventEmitter<any> = new EventEmitter<any>();
  public actualizarInfo: EventEmitter<any> = new EventEmitter<any>();
  public abrirModal: EventEmitter<any> = new EventEmitter<any>();
  public apiUrl: string = environment.apiUrl;

  constructor() { }

  setTema(tema) {
    document.getElementById('indexBody').setAttribute('data-theme', tema);
  }



}
