import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AplicationService } from 'src/app/services/aplication.service';

@Component({
  selector: 'app-formulario-mascota',
  templateUrl: './formulario-mascota.component.html'
})
export class FormularioMascotaComponent implements OnInit {

  public formMascota: FormGroup;

  constructor(public appService: AplicationService) { }

  ngOnInit(): void {
    this.inicializarFormularioMascota();
  }


  
  // Formulario de datos necesarios para crear una mascota.
  inicializarFormularioMascota() {

      this.formMascota = new FormGroup({
        nombreMascota: new FormControl( null, Validators.required),
        apellidosMascota: new FormControl( null, Validators.required),
        especie: new FormControl( null, [Validators.required]),
        raza: new FormControl( null, Validators.required),
        color: new FormControl( null, Validators.required),
        sexo: new FormControl( 'sexo', Validators.required),
        fechaNacimiento: new FormControl( null, Validators.required),
        esterilizado: new FormControl( null, Validators.required),
      });
    }

}
