import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { AplicationService } from 'src/app/services/aplication.service';
import { SettingsService } from 'src/app/services/settings.service';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styles: [
  ]
})
export class HeaderComponent implements OnInit {

  @Input() user;
  @Input() apiUrl: string;
  @Output() abrirModal = new EventEmitter<boolean>();


  constructor(public userService: UserService,
              private appService: AplicationService,
              private settingsService: SettingsService) { }
    

  ngOnInit(): void { 
    // this.user = JSON.parse(this.userService.getLocalStorage('user'));
    // console.log(this.user);

    this.settingsService.actualizarInfo.subscribe( () => {
      this.user = JSON.parse(this.userService.getLocalStorage('user'));
    });
  }

  cambiarContrasena() {
    this.abrirModal.emit(true);
  }

  miPerfil() {
    let usuario;
    if (this.user.tipoCuenta === 'empresa') {
      usuario = this.user.tipoEmpresa;
    } else {
      usuario = this.user.tipoCuenta;
    }

    this.appService.redireccion(usuario, 'perfil');
  }

  cancelarCuenta() {
    // console.log('aquiii cancelar cuenta');
    let mensaje: string;

    if (this.user.tipoCuenta === 'empresa') {
      mensaje = '¿ Estas seguro que deseas cancelar tu cuenta ?. Al cancelar tu cuenta se eliminaran los consultorios que tengas creados, tus servicios pasaran a estar inactivos y ya no seran visibles para los usuarios en la aplicación móvil.';
    }

    if (this.user.tipoCuenta === 'medico') {
      mensaje = '¿ Estas seguro que deseas cancelar tu cuenta ?. Al cancelar tu cuenta se eliminaran tu perfil médico de los proveedores en los cuales se encuentre asociado y ya no sera visible para los usuarios en la aplicación móvil';
    }

    if (this.user.tipoCuenta === 'empleado') {
      mensaje = '¿ Estas seguro que deseas cancelar tu cuenta ?. Al cancelar tu cuenta se eliminaran tu perfil administrativo de los proveedores en los cuales se encuentre asociado y no podras disfrutar de los beneficios de  usuario desde la aplicación móvil.';
    }

    this.appService.swalConfirm('Advertencia', mensaje, 'warning').then(
      () => {
        this.appService.cancelarCuenta(this.user.idMembers).subscribe();
      });
  }

}
