import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { InfoServicio } from 'src/app/interfaces/servicios';
import { SettingsService } from 'src/app/services/settings.service';
import { SucursalesService } from 'src/app/services/sucursales.service';
import { CitaMascota } from '../../../interfaces/mascotas';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AplicationService } from '../../../services/aplication.service';
import { InfoMedico } from 'src/app/interfaces/medicos';
import { PermisosService } from 'src/app/services/permisos.service';
import { TipoDocumento } from 'src/app/interfaces/info';
import { FormularioMascotaComponent } from './formulario-mascota/formulario-mascota.component';
import * as moment from 'moment';

@Component({
  selector: 'app-citas-mascotas',
  templateUrl: './citas-mascotas.component.html'
})
export class CitasMascotasComponent implements OnInit {

  @ViewChild(FormularioMascotaComponent) formMascota: FormularioMascotaComponent;
  @Input() user;
  @Output() citasEnCurso = new EventEmitter<string>();
  public estado: string;
  public estadoSacarCita: string;
  public numeroDocumento: number;
  public mascotas;
  public citasMascota: CitaMascota[]; 
  public citaActiva: CitaMascota;
  public serviciosActivos: InfoServicio[];
  public formMascotaExiste: FormGroup;
  public horarios;
  public medicosActivos: InfoMedico[];
  public hoy;
  public formMember: FormGroup;
  public mascotaSelect = new FormControl('mascota', Validators.required);
  public tipoDocumento: TipoDocumento[];
 

  constructor(public settingsService: SettingsService,
              private sucursalService: SucursalesService,
              private appService: AplicationService,
              private permisos: PermisosService) { }

  ngOnInit(): void {

    // console.log(this.user);
    this.hoy = this.appService.hoy('hoy', '', 0);
    this.settingsService.abrirModal.subscribe( (info: { numeroDocumento: number, idSucursal: number }) => {
      this.getCitasMascotas(info);
    });

    this.mascotaSelect.valueChanges.subscribe( () => {
      this.seleccionaMascota();
    });
  }


  // <!-- ----------------------------------------------------------------------------------------------------- -->
  // <!-- ----------------------------------------- Formularios ----------------------------------------------- -->
  // <!-- ----------------------------------------------------------------------------------------------------- -->

    // Formulario de datos necesarios para sacar una cita.
    inicializarFormularioCita() {

      this.formMascotaExiste = new FormGroup({
        servicio: new FormControl( 'servicio', Validators.required  ),
        fecha: new FormControl( null, Validators.required  ),
        medico: new FormControl( 'medico', Validators.required  ),
      });
    }


    // Formulario de datos necesarios para crear un usuario.
    inicializarFormularioMember() {

      this.formMember = new FormGroup({
        nombres: new FormControl( null, Validators.required),
        apellidos: new FormControl( null, Validators.required),
        correo: new FormControl( null, [Validators.required, Validators.email]),
        telefono: new FormControl( null, Validators.required),
        tipoDocumento: new FormControl( 'tipo_documento', Validators.required),
        numeroDocumento: new FormControl( this.numeroDocumento, Validators.required),
        fechaNacimiento: new FormControl( null, Validators.required),
      });
    }


  // <!-- ----------------------------------------------------------------------------------------------------- -->
  // <!-- --------------------------------------- Fin Formularios --------------------------------------------- -->
  // <!-- ----------------------------------------------------------------------------------------------------- -->

  /*
  * Método que permite obtener las citas de la mascota por numero de documento del dueño
  */
  getCitasMascotas(info: { numeroDocumento: number, idSucursal: number }) {

    this.estado = '';
    this.numeroDocumento = info.numeroDocumento;
    this.sucursalService.getCitasMascotaDocumentoUser(info).subscribe( res => {
      
      if ( res.length <= 0 ) { 
        this.estado = 'sin_citas';
        document.getElementById('btn-citas-activas-mascotas').click();
        return;
      }

      this.estado = 'citas_activas';
      this.citasMascota = res;
      document.getElementById('btn-citas-activas-mascotas').click();
    });

  }


  // Metodo para buscar las mascotas e informacion de un usuario a traves de su numero de documento
  buscarMascotas () {
    this.sucursalService.getMascotasPorNumeroDocumento(this.numeroDocumento).subscribe( res => {

      // Validacion, si el numero de documento ingresado pertenece a una empresa, no es posible sacar cita
      if (res.user && res.user.tipoCuenta === 'empresa') {
        this.appService.swal('', `No es posible sacar una cita. El número de documento ${this.numeroDocumento} pertenece a una cuenta de empresa.`, 'warning');
        document.getElementById('btn-cerrar-modalcitasActi2').click();
        return;
      }

      // Casos para sacar una cita a mascota

      // Caso 1: El usuario no existe y hay que crear el usuario, un grupo familiar y la mascota
      if (res.mascotas.length == 0 && !res.user) { 
        this.inicializarFormularioMember();
        this.getTiposDocumento();
        this.estadoSacarCita = 'new_user_mascota';
      }

      // Caso 2: El usuario existe pero no tiene ninguna mascota registrada, hay que crear la mascota
      if (res.user && res.mascotas.length <= 0) {
        this.estadoSacarCita = 'new_mascota';
      }

      // Caso 3: El usuario existe y tambien tiene mascotas registradas
      if (res.user && res.mascotas.length >= 1) {
        this.estadoSacarCita = 'existen';
      }

      // Inicializacion de variables y formulario
        this.inicializarFormularioCita();
        this.mascotas = res;
        this.estado = 'sacar_cita';
        this.getServiciosActivosSucursal();
        // console.log(res);
    });

  }


  reprogramarCita(citaActiva: CitaMascota) {

    this.inicializarFormularioCita();
    this.estado = 'reprogramar';
    this.getServiciosActivosSucursal();
    this.citaActiva = citaActiva;
  }
  
  // // Metodo para llama los servicios activos que tiene una sucursal para sacar cita.
  getServiciosActivosSucursal() {

    this.formMascotaExiste.get('servicio').setValue('servicio');
    this.sucursalService.getServiciosActivosSucursal({idMunicipio: this.user.idmunicipioSucursal, idSucursal: this.user.idSucursales})
    .subscribe( res => {
       this.serviciosActivos = res;
    });
  }

  // // Metodo para obtener los medicos activos segun el servicio.
  getMedicosActivosSucursal(ev) {
    // this.horarios = undefined;
    this.sucursalService.getMedicosActivosServicio({idServicio: ev.target.value, idMunicipio: this.user.idmunicipioSucursal, idSucursal: this.user.idSucursales})
                        .subscribe( res => { 
                          this.medicosActivos = res;
                          this.formMascotaExiste.get('medico').setValue('medico');
                        });
  }

  validarDatosGetHorario () {

    if (this.formMascotaExiste.get('fecha').invalid) {
      return;
    }

    else if ((this.formMascotaExiste.get('servicio').value === 'servicio' )) {
      return;
    }

    else if (this.formMascotaExiste.get('medico').value === 'medico') {
      return;
    } else {
      this.buscarHorario();
    }


  }


  // Metodo para buscar un horario de atención para sacar cita.
  buscarHorario() {

    if (this.formMascotaExiste.get('fecha').invalid) {
      this.appService.swal('Advertencia', 'Por favor escoge una fecha para la cita.', 'warning');
      return;
    }

    if ((this.formMascotaExiste.get('servicio').value === 'servicio' )) {
      this.appService.swal('Advertencia', 'Por favor escoge un servicio.', 'warning');
      return;
    }

    if (this.formMascotaExiste.get('medico').value === 'medico') {
      this.appService.swal('Advertencia', 'Por favor escoge un médico..', 'warning');
      return;
    }

    this.sucursalService.getHorario({ fecha: this.formMascotaExiste.get('fecha').value,
                                      idSucursal : this.user.idSucursales,
                                      idServicio : this.formMascotaExiste.get('servicio').value,
                                      idMedico : this.formMascotaExiste.get('medico').value })
                  .subscribe( res => {this.horarios = res ;
                  document.getElementById('select-medico').className = 'form-control';
                });

  }


    // Metodo para reprogramar una cita, recibe como parametros la hora en la que se desea sacar la cita, el
  // id del consultorio y el id de horario.
  updateCita(hora, idConsultorios, idHorario) {

    if (this.appService.hoy('hoy', '', 0) === this.formMascotaExiste.get('fecha').value) {
      let h = hora.split(' ');
      if ( parseInt(moment(new Date()).format('HH'))  > parseInt(h[0]) ) {

        this.appService.swal('Advertencia', 'La hora seleccionada ya paso, por favor escoge otro horario de atención.', 'warning');
        return;
      }

    }


    // REPROGRAMAR CITA
    if (this.estado === 'reprogramar') {

          // Validacion de edicion
    if(this.permisos.permisos(0, 0, 0, 'editar')) {


      if (!this.formMascotaExiste.get('fecha').value ||
        !this.formMascotaExiste.get('servicio').value ||
        ( this.formMascotaExiste.get('medico').value === 'medico' ) ) {
          this.appService.swal('Advertencia', 'Por favor completa, la fecha el servicio y el médico para reprogramar la cita.', 'warning');
          return;
      }


    let info = {
        idCitasMascotasActivas: this.citaActiva.idCitasMascotasActivas,
        fecha: this.formMascotaExiste.get('fecha').value + ' ' + hora.split(' ')[0] + ':00:00',
        hora,
        quienSacoCita : this.user.idMembers,
        idServicios : this.formMascotaExiste.get('servicio').value,
        idConsultorios,
        _idHorario: idHorario,
        idSucursales: this.user.idSucursales,
        idMedico : this.formMascotaExiste.get('medico').value,
        idMascotas : this.citaActiva.idMascotas
    };


    let titulo = `¿ Estas seguro que deseas reprogramar la cita para el ${this.formMascotaExiste.get('fecha').value}, a las ${hora} ?`;



    this.appService.swalConfirm('Reprogramar cita', titulo, 'warning').then(() => {

      // console.log(info);
      this.sucursalService.putReprogramarCitaMascota(info).subscribe( () => {
        document.getElementById('btn-cerrar-modalcitas').click();
        location.reload();
      });
    }); 


    }

    } else {
      this.sacarCitaMascota(hora, idConsultorios, idHorario);
    }


  }


  cancelarCita(idCitaMascota, tabla) {

    if( this.permisos.permisos(0, 0, 0, 'eliminar')) {

      this.appService.swalConfirm('Cancelar cita', '¿ Estas seguro que deseas cancelar la cita ?', 'warning')
      .then( () => {
        this.sucursalService.postCancelarCita({idCitaEnCurso: idCitaMascota , motivo: 'Cita cancelada por el personal de la empresa.', 
        tabla, cancelada : 1, tipo: 'mascota'}).subscribe( () => {
          // this.getCitasEnCurso();
          document.getElementById('btn-cerrar-modalcitasActi').click();
        });
      });

      
    }
  }


  agregarCita (idCitaActiva) {
    // console.log(idCitaActiva)
    this.sucursalService.postActivarCita({idCitaActiva, tipo: 'mascota'}).subscribe( () => {
      this.citasEnCurso.emit();
      document.getElementById('btn-cerrar-modalcitasActi').click();
      document.getElementById('btn-cerrar-modalcitas').click();
    });
  }

  seleccionaMascota() {


    // console.log(this.mascotaSelect.value);
    if(this.mascotaSelect.value === 'nueva') {
      document.getElementById('mascota-form').style.display = '';
      document.getElementById('mascota-avatar').style.display = 'none';
    } else {
      document.getElementById('mascota-form').style.display = 'none';
      document.getElementById('mascota-avatar').style.display = '';
    }
  }

  sacarCitaMascota(hora, idConsultorios, idHorario) {
    // console.log('sacar cita' , hora, idConsultorios, idHorario);
    var info;

      // Casos para sacar una cita a mascota

      // // Caso 1: El usuario no existe y hay que crear el usuario, un grupo familiar y la mascota
      if (this.estadoSacarCita === 'new_user_mascota') { 
          // console.log(this.formMember.value);
          // console.log(this.formMascota.formMascota.value);

          if(this.formMascota.formMascota.invalid) return this.appService.swal('', 'Por favor completa todos los datos de la mascota.', 'warning');
          if(this.formMember.invalid) return this.appService.swal('', 'Por favor completa todos los datos del usuario.', 'warning');

          info = {
            caso: 'new_user_mascota',
            usuario : this.formMember.value,
            mascota : this.formMascota.formMascota.value,
            cita : {
              fecha: this.formMascotaExiste.get('fecha').value + ' ' + hora.split(' ')[0] + ':00:00',
              hora,
              quienSacoCita : this.user.idMembers,
              idServicios : this.formMascotaExiste.get('servicio').value,
              idConsultorios,
              _idHorario: idHorario,
              idSucursales: this.user.idSucursales,
              idMedico : this.formMascotaExiste.get('medico').value
            }
          };

        
      }

      // // Caso 2: El usuario existe pero no tiene ninguna mascota registrada, hay que crear la mascota
      if (this.estadoSacarCita === 'new_mascota') {
        // console.log(this.formMascota.formMascota.value);

        if(this.formMascota.formMascota.invalid) return this.appService.swal('', 'Por favor completa todos los datos de la mascota.', 'warning');
        info = {
          caso: 'new_mascota',
          mascota : this.formMascota.formMascota.value,
          usuario: { 
            idMembers: this.mascotas.user.idMembers,
            nombres: this.mascotas.user.nombres,
            apellidos: "",
          },
          cita : {
            fecha: this.formMascotaExiste.get('fecha').value + ' ' + hora.split(' ')[0] + ':00:00',
            hora,
            quienSacoCita : this.user.idMembers,
            idServicios : this.formMascotaExiste.get('servicio').value,
            idConsultorios,
            _idHorario: idHorario,
            idSucursales: this.user.idSucursales,
            idMedico : this.formMascotaExiste.get('medico').value
          }
        };


      }

      // // Caso 3: El usuario existe y tambien tiene mascotas registradas
      if (this.estadoSacarCita === 'existen') {

        if (this.mascotaSelect.value === 'nueva') {
          
          if(this.formMascota.formMascota.invalid) return this.appService.swal('', 'Por favor completa todos los datos de la mascota.', 'warning');
          info = {
            caso: 'new_mascota_existe_user',
            mascota : this.formMascota.formMascota.value,
            usuario: { 
              idMembers: this.mascotas.user.idMembers,
              nombres: this.mascotas.user.nombres,
              apellidos: "",
            },
            cita : {
              fecha: this.formMascotaExiste.get('fecha').value + ' ' + hora.split(' ')[0] + ':00:00',
              hora,
              quienSacoCita : this.user.idMembers,
              idServicios : this.formMascotaExiste.get('servicio').value,
              idConsultorios,
              _idHorario: idHorario,
              idSucursales: this.user.idSucursales,
              idMedico : this.formMascotaExiste.get('medico').value
            }
          };
        } else {
          // Mascota existe

          if(!this.mascotaSelect.value.idMascotas) return this.appService.swal('', 'Por favor antes de sacar la cita selecciona una mascota', 'warning');

          info = {
            caso: 'existe_mascota',
            cita : {
              fecha: this.formMascotaExiste.get('fecha').value + ' ' + hora.split(' ')[0] + ':00:00',
              hora,
              quienSacoCita : this.user.idMembers,
              idServicios : this.formMascotaExiste.get('servicio').value,
              idConsultorios,
              _idHorario: idHorario,
              idSucursales: this.user.idSucursales,
              idMedico : this.formMascotaExiste.get('medico').value,
              idMascota: this.mascotaSelect.value.idMascotas
            }
          };
        }
        
      }


      // console.log(info);
      let titulo = `¿ Estas seguro que deseas programar la cita para el ${this.formMascotaExiste.get('fecha').value}, a las ${hora} ?`;

      this.appService.swalConfirm('Sacar cita', titulo, 'warning').then(() => {
              // console.log(info);
          this.sucursalService.postSacarCitaMascota(info).subscribe( () => {
              document.getElementById('btn-cerrar-modalcitasActi').click();
          });
      }); 


  }

  getTiposDocumento() {
    this.appService.getTiposDocumento().subscribe( res => this.tipoDocumento = res );
  }


}
