import { Component, Input, OnInit, Output, EventEmitter } from "@angular/core";
import { FormControl, Validators } from "@angular/forms";
import { FormGroup } from "@angular/forms";
import { InfoConsultorio, Sucursal } from "src/app/interfaces/sucursales";
import { SucursalesService } from "src/app/services/sucursales.service";
import { EmpresaService } from "../../../services/empresa.service";
import { InfoServicio } from "../../../interfaces/servicios";
import { AplicationService } from "src/app/services/aplication.service";
import { MedicosService } from "src/app/services/medicos.service";
import { InfoMedico } from "../../../interfaces/medicos";
import { UserService } from "src/app/services/user.service";
import { PermisosService } from "src/app/services/permisos.service";
import { SettingsService } from "src/app/services/settings.service";

@Component({
  selector: "app-consultorios",
  templateUrl: "./consultorios.component.html",
  styles: [],
})
export class ConsultoriosComponent implements OnInit {
  @Input() user; 
  @Input() apiUrl: string;
  @Input() crearEmpresa: boolean;
  @Output() cambioEstado = new EventEmitter<string>();

  public sucursales: Sucursal[];
  public sucursal: Sucursal;
  public medicos: InfoMedico[];
  public servicios: InfoServicio;
  public dias = [];
  public horarios = [];
  public consultorios: any;
  public formConsultorio: FormGroup;
  public estado = "crear";
  public horariosEdit;
  public editHorario;
  public horario;
  public consultorio;
  public formEditHorario: FormGroup;
  public crearHorario = "no_crear";
  load = false;
  register = false;

  constructor(
    private sucursalesService: SucursalesService,
    private empresaService: EmpresaService,
    private appService: AplicationService,
    private userService: UserService,
    private medicosService: MedicosService,
    private permisos: PermisosService,
    private settingsService: SettingsService
  ) {}

  ngOnInit(): void {
    this.setTimeLoad();
    if (this.crearEmpresa) {
      this.appService.setBreadcrumb("Paso 4 - Crear Consultorio");
    }

    this.settingsService.actualizarInfo.subscribe(() => {
      this.user = JSON.parse(localStorage.getItem("user"));
      this.getSucursales();
    });

    this.getInfo();
  }

  
  setTimeLoad() {
    this.load = true;
    setTimeout(() => {
      (this.load = false), (this.register = true);
    }, 500);
  }
  getInfo() {
    // this.createHorario();
    this.getSucursales();
    // this.getConsultorios();
    this.getMedicos();
    this.getServicios();
    this.iniciarlizarFormulario();
  }

  getSucursales() {

    this.sucursalesService
      .getSucursales(this.user.idEmpresa)
      .subscribe((res: any) => {
        res.map((sucursal: Sucursal) => {
          if (sucursal.idSucursales === this.user.idSucursales) {
            // console.log(sucursal);
            this.sucursal = sucursal;
            return this.getConsultorios();
          }
        });
      });
  }

  getMedicos() {
    this.medicosService
      .getMedicos(this.user.idSucursales)
      .subscribe((res) => (this.medicos = res));
  }

  getServicios() {
    this.empresaService
      .getServicios(this.user.idEmpresa)
      .subscribe((res) => (this.servicios = res));
  }

  getConsultorios() {
    // console.log(this.sucursales);
    this.empresaService
      .getConsultorios(this.user.idSucursales)
      .subscribe((res) => (this.consultorios = res));
  }

  validada() {
    if (this.consultorios.length >= 1) {
      this.appService.validada(this.user.idMembers).subscribe(() => {
        this.userService
          .getInfoUser({ idMembers: this.user.idMembers, tipoUser: "empresa" })
          .subscribe(() => {
            localStorage.removeItem("estado");
            this.cambioEstado.emit("validada");
          });
      });

      return;
    }

    this.appService.swal(
      "Advertencia",
      "Por favor crea al menos un consultorio.",
      "warning"
    );
  }

  eliminarConcultorioAlert(consultorio) {
    if (this.permisos.permisos(1, 1, 1, "eliminar")) {
      // console.log(consultorio);
      this.appService
        .swalConfirm(
          "Eliminar consultorio",
          "¿ Estas seguro que deseas eliminar el consultorio ?",
          "warning"
        )
        .then(() => {
          this.empresaService
            .dltConsultorio(consultorio.consultorio.idConsultorios)
            .subscribe(() => this.getConsultorios());
        });
    }
  }

  // <!-- ----------------------------------------------------------------------------------------------------- -->
  // <!-- ---------------------------------------- Metodos horarios ------------------------------------------- -->
  // <!-- ----------------------------------------------------------------------------------------------------- -->

  iniciarConsultorioInfo() {
    if (this.permisos.permisos(1, 1, 1, "crear")) {
      if (
        this.user.tipoEmpresa === "Consultorio" &&
        this.consultorios &&
        this.consultorios.length >= 2
      ) {
        this.appService.swal(
          "Advertencia",
          "Has alcansado el número maximo de consultorios.",
          "warning"
        );
        return;
      }

      this.horarios = [];
      this.formConsultorio.reset();
      this.setDias();
      this.createHorario();
      this.estado = "crear";
      document.getElementById("btn-consultorios").click();
    }
  }

  iniciarlizarFormulario() {
    this.formConsultorio = new FormGroup({
      nombre: new FormControl(null, Validators.required),
      extencion: new FormControl(null),
    });
  }

  setDias() {
    this.dias = [
      { dia: "Lunes", value: "Monday", disponible: true, checked: false },
      { dia: "Martes", value: "Tuesday", disponible: true, checked: false },
      {
        dia: "Miércoles",
        value: "Wednesday",
        disponible: true,
        checked: false,
      },
      { dia: "Jueves", value: "Thursday", disponible: true, checked: false },
      { dia: "Viernes", value: "Friday", disponible: true, checked: false },
      { dia: "Sábado", value: "Saturday", disponible: true, checked: false },
      { dia: "Domingo", value: "Sunday", disponible: true, checked: false },
    ];
  }

  createHorario() {
    this.horarios.push({
      horario: 1,
      disabled: false,
      dias: [],
      manana: [
        {
          disponible: false,
          desde: null,
          hasta: null,
          servicio: null,
          medico: null,
          numeroCitas: null,
          ok: false,
        },
      ],
      tarde: [
        {
          disponible: false,
          desde: null,
          hasta: null,
          servicio: null,
          medico: null,
          numeroCitas: null,
          ok: false,
        },
      ],
    });
  }

  cambioDia(ev, horario, posicionDia) {
    // console.log(horario);

    if (ev.target.checked) {
      this.horarios[horario - 1].dias.push(ev.target.name);
      this.dias[posicionDia].checked = true;
    } else {
      let index = this.horarios[horario - 1].dias.indexOf(ev.target.name);
      this.horarios[horario - 1].dias.splice(index, 1);
      this.dias[posicionDia].checked = false;
    }

    // console.log(this.posicionDias);
    // this.dias[posicionDia].disponible = false;
  }

  desdeHasta(ev, hora, desde, hasta, servicio, medico, horario) {
    // console.log(horario);
    if (hora === "manana") {
      if (desde) {
        this.horarios[horario].manana[0].desde = ev.target.value;
        // this.horasManana.desde = ev.target.value;
      }

      if (hasta) {
        this.horarios[horario].manana[0].hasta = ev.target.value;
        // this.horasManana.hasta = ev.target.value;
      }

      if (
        this.horarios[horario].manana[0].desde &&
        this.horarios[horario].manana[0].hasta
      ) {
        this.desdeHastaValidation({
          hasta: this.horarios[horario].manana[0].hasta,
          desde: this.horarios[horario].manana[0].desde,
        });
      }

      if (servicio) {
        this.horarios[horario].manana[0].servicio = ev.target.value;
        this.numeroCitas(ev.target.value).then(
          (ns) => (this.horarios[horario].manana[0].numeroCitas = ns)
        );
      }

      if (medico) {
        // console.log('aquiiii medico', medico, ev.target.value);
        this.horarios[horario].manana[0].medico = ev.target.value;
        // this.horasManana.medico = ev.target.value;
      }

      if (
        this.desdeHastaValidation({
          hasta: this.horarios[horario].manana[0].hasta,
          desde: this.horarios[horario].manana[0].desde,
        }) &&
        this.horarios[horario].manana[0].servicio &&
        this.horarios[horario].manana[0].medico
      ) {
        this.horarios[horario].manana[0].ok = true;
        // this.horasManana.ok = true;
      } else {
        this.horarios[horario].manana[0].ok = false;
      }

      // console.log(this.horarios);
      return;
    }

    if (hora === "tarde") {
      if (desde) {
        // this.horasTarde.desde = ev.target.value;
        this.horarios[horario].tarde[0].desde = ev.target.value;
      }

      if (hasta) {
        // this.horasTarde.hasta = ev.target.value;
        this.horarios[horario].tarde[0].hasta = ev.target.value;
      }

      if (servicio) {
        // this.horasTarde.servicio = ev.target.value;
        // this.numeroCitas(hora, ev.target.value);
        this.horarios[horario].tarde[0].servicio = ev.target.value;
        this.numeroCitas(ev.target.value).then(
          (ns) => (this.horarios[horario].tarde[0].numeroCitas = ns)
        );
      }

      if (medico) {
        // this.horasTarde.medico = ev.target.value;
        this.horarios[horario].tarde[0].medico = ev.target.value;
      }

      if (
        this.horarios[horario].tarde[0].desde &&
        this.horarios[horario].tarde[0].hasta
      ) {
        this.desdeHastaValidation({
          hasta: this.horarios[horario].tarde[0].hasta,
          desde: this.horarios[horario].tarde[0].desde,
        });
      }

      if (
        this.desdeHastaValidation({
          hasta: this.horarios[horario].tarde[0].hasta,
          desde: this.horarios[horario].tarde[0].desde,
        }) &&
        this.horarios[horario].tarde[0].servicio &&
        this.horarios[horario].tarde[0].medico
      ) {
        this.horarios[horario].tarde[0].ok = true;
        // this.horasManana.ok = true;
      } else {
        this.horarios[horario].tarde[0].ok = false;
      }
    }

    // console.log(this.horarios);
  }

  cambioHora(ev, horario, hora) {
    if (hora === "manana") {
      this.horarios[horario].manana[0].disponible = ev;

      if (!ev) {
        this.horarios[horario].manana[0].ok = ev;
        this.horarios[horario].manana[0].desde = null;
        this.horarios[horario].manana[0].hasta = null;
        this.horarios[horario].manana[0].servicio = null;
        this.horarios[horario].manana[0].medico = null;
        this.horarios[horario].manana[0].numeroCitas = null;
      }
      return;
    }

    if (hora === "tarde") {
      this.horarios[horario].tarde[0].disponible = ev;

      if (!ev) {
        this.horarios[horario].tarde[0].ok = ev;
        this.horarios[horario].tarde[0].desde = null;
        this.horarios[horario].tarde[0].hasta = null;
        this.horarios[horario].tarde[0].servicio = null;
        this.horarios[horario].tarde[0].medico = null;
        this.horarios[horario].tarde[0].numeroCitas = null;
      }
    }

    // console.log(this.horarios);
  }

  numeroCitas(idServicio) {
    return new Promise((resolve, reject) => {
      let servicios: any = this.servicios;
      servicios.forEach((s) => {
        if (s.idServicios.toString() === idServicio) {
          //  numeroCitas =  s.numeroCitas;
          return resolve(s.numeroCitas);
        }
      });
    });
  }

  desdeHastaValidation(horas): boolean {
    // console.log(horas);
    if (parseInt(horas.desde) > parseInt(horas.hasta)) {
      this.appService.swal(
        "Advertencia",
        "La hora de inicio no puede ser mayor a la hora de finalización.",
        "warning"
      );
      return false;
    }
    return true;
  }

  agregarHorario(enviar: boolean, horario) {
    // console.log(horario, this.horarios[horario]);

    return new Promise((resolve, reject) => {
      // Validaciones horarios
      if (this.horarios[horario].dias.length <= 0) {
        this.appService.swal(
          "Advertencia",
          "Por favor selecciona los días de atención en el horario " +
            (horario + 1) +
            ".",
          "warning"
        );
        return reject(false);
      }

      // Si no se escogio ni mañana ni tarde
      if (
        !this.horarios[horario].manana[0].disponible &&
        !this.horarios[horario].tarde[0].disponible
      ) {
        this.appService.swal(
          "Advertencia",
          "Por favor selecciona tu horario de atención.",
          "warning"
        );
        return reject(false);
      }

      if (
        this.horarios[horario].manana[0].disponible &&
        !this.horarios[horario].manana[0].ok
      ) {
        this.appService.swal(
          "Advertencia",
          "Por favor selecciona tu horario de atención en la mañana, médico y servicio.",
          "warning"
        );
        return reject(false);
      }

      if (
        this.horarios[horario].tarde[0].disponible &&
        !this.horarios[horario].tarde[0].ok
      ) {
        this.appService.swal(
          "Advertencia",
          "Por favor selecciona tu horario de atención en la tarde, médico y servicio.",
          "warning"
        );
        return reject(false);
      }

      if (
        !this.horarios[horario].manana[0].ok &&
        this.horarios[horario].manana[0].disponible
      ) {
        this.appService.swal(
          "Advertencia",
          "Por favor revisa tu horario de atención en la mañana, médico y servicio.",
          "warning"
        );
        return reject(false);
      }

      if (
        !this.horarios[horario].tarde[0].ok &&
        this.horarios[horario].tarde[0].disponible
      ) {
        this.appService.swal(
          "Advertencia",
          "Por favor revisa tu horario de atención en la tarde, médico y servicio.",
          "warning"
        );
        return reject(false);
      }

      if (enviar) {
        return resolve(true);
      }

      this.addHorario();
      // if ( this.addHorario(enviar)) {
      //   return resolve(true);
      // }
    });
  }

  addHorario() {
    // console.log(this.horarios);

    this.disponibleDias(this.horarios[this.horarios.length - 1].dias, false);

    // Saber si quedan dias disponibles
    if (!this.lengthDias()) {
      this.appService.swal(
        "Advertencia",
        "No hay días disponibles para crear otro horario.",
        "warning"
      );
      this.disponibleDias(this.horarios[this.horarios.length - 1].dias, true);
      return;
    }

    // Inhabilitar el horario que fue correcto
    this.horarios[this.horarios.length - 1].disabled = true;

    // crear otro horario
    this.horarios.push({
      horario: this.horarios.length + 1,
      disabled: false,
      dias: [],
      manana: [
        {
          disponible: false,
          desde: null,
          hasta: null,
          servicio: null,
          medico: null,
          numeroCitas: null,
        },
      ],
      tarde: [
        {
          disponible: false,
          desde: null,
          hasta: null,
          servicio: null,
          medico: null,
          numeroCitas: null,
        },
      ],
    });
  }

  crearHorarioEdit() {
    // console.log('aqui horario nuevo');
    this.setDias();
    // tslint:disable-next-line: prefer-for-of
    for (let i = 0; i < this.horarios.length; i++) {
      this.disponibleDias(this.horarios[i].dias, false);
    }

    // Saber si quedan dias disponibles
    if (!this.lengthDias()) {
      this.appService.swal(
        "Advertencia",
        "No hay días disponibles para crear otro horario.",
        "warning"
      );
      return;
    }

    this.horarios.push({
      horario: this.horarios.length + 1,
      disabled: false,
      dias: [],
      manana: [
        {
          disponible: false,
          desde: null,
          hasta: null,
          servicio: null,
          medico: null,
          numeroCitas: null,
        },
      ],
      tarde: [
        {
          disponible: false,
          desde: null,
          hasta: null,
          servicio: null,
          medico: null,
          numeroCitas: null,
        },
      ],
    });

    // console.log(this.horarios);

    this.horario = {
      horario: this.horarios.length,
      posicion: this.horarios.length - 1,
      disabled: false,
      dias: [],
      manana: [
        {
          disponible: false,
          desde: null,
          hasta: null,
          servicio: null,
          medico: null,
          numeroCitas: null,
        },
      ],
      tarde: [
        {
          disponible: false,
          desde: null,
          hasta: null,
          servicio: null,
          medico: null,
          numeroCitas: null,
        },
      ],
    };

    this.editFormEditarHorario(false);
    this.editHorario = "editar";
    this.crearHorario = "crear";
  }

  // Metodo para habilitar o deshabilitar dias
  checkedDias(diasHorario, estado) {
    // console.log(this.dias);
    // Desabilitar los dias que ya han sido escogidos
    for (let i = 0; i < diasHorario.length; i++) {
      for (let j = 0; j < this.dias.length; j++) {
        if (diasHorario[i] === this.dias[j].value) {
          this.dias[j].checked = estado;
        }
      }
    }
  }

  // Metodo para verificar que queden días disponibles
  lengthDias(): boolean {
    // tslint:disable-next-line: prefer-for-of
    for (let i = 0; i < this.dias.length; i++) {
      if (this.dias[i].disponible) {
        return true;
      }
    }
    return false;
  }

  borrarHorario(horario) {
    this.checkedDias(this.horarios[horario - 1].dias, false);

    // Eliminar el ultimo horario
    this.horarios.splice(horario - 1, 1);

    // Habilitar el horario
    this.horarios[horario - 2].disabled = false;

    // Habilitar los dias
    this.disponibleDias(this.horarios[horario - 2].dias, true);
  }

  // Metodo para habilitar o deshabilitar dias
  disponibleDias(diasHorario, estado) {
    // console.log(this.dias);
    // Desabilitar los dias que ya han sido escogidos
    for (let i = 0; i < diasHorario.length; i++) {
      for (let j = 0; j < this.dias.length; j++) {
        if (diasHorario[i] === this.dias[j].value) {
          this.dias[j].disponible = estado;
        }
      }
    }

    // console.log(this.dias);
  }

  crearConsultorio() {
    if (this.estado === "editar") {
      // console.log('consul', this.consultorio);
      let info = {
        nombreConsultorio: this.formConsultorio.get("nombre").value,
        extensionConsultorio: this.formConsultorio.get("extencion").value,
        idConsultorios: this.consultorio.idConsultorios,
      };
      this.empresaService.putConsultorio(info).subscribe(() => {
        document.getElementById("cerrar-consultorio-modal").click();
      });
      return;
    }

    this.agregarHorario(true, this.horarios.length - 1)
      .then(() => {
        // Informacion del consultorios
        var consultorioInfo: InfoConsultorio = {
          nombreConsultorio: this.formConsultorio.get("nombre").value,
          extensionConsultorio: this.formConsultorio.get("extencion").value,
          idSucursales: this.sucursal.idSucursales,
          idmunicipio: this.sucursal.idmunicipio,
          creadoPor: this.user.idMembers,
        };

        // console.log(this.horarios);
        // console.log(this.dias);

        // Horarios
        var horariosEnviar = [];
        this.horarios.forEach(function (horarioEach, indice) {
          var mananaHoras = [];
          var tardeHoras = [];
          var servicios = [];
          var medicos = [];

          if (horarioEach.manana[0].disponible) {
            servicios.push(horarioEach.manana[0].servicio);
            medicos.push(horarioEach.manana[0].medico);

            let diferenciaManana =
              1 +
              (parseInt(horarioEach.manana[0].hasta) -
                parseInt(horarioEach.manana[0].desde));

            for (let m = 0; m < diferenciaManana; m++) {
              let hora = parseInt(horarioEach.manana[0].desde) + m;
              mananaHoras.push({
                disponible: true,
                hora: hora + " a.m",
                numeroCitas: horarioEach.manana[0].numeroCitas,
              });
            }
            // console.log('manana', manana);
          }

          if (horarioEach.tarde[0].disponible) {
            servicios.push(horarioEach.tarde[0].servicio);
            medicos.push(horarioEach.tarde[0].medico);

            let diferenciaTarde =
              1 +
              (parseInt(horarioEach.tarde[0].hasta) -
                parseInt(horarioEach.tarde[0].desde));

            for (let m = 0; m < diferenciaTarde; m++) {
              let hora = parseInt(horarioEach.tarde[0].desde) + m;
              tardeHoras.push({
                disponible: true,
                hora: hora + " p.m",
                numeroCitas: horarioEach.tarde[0].numeroCitas,
              });
            }
            // console.log('tarde', tarde);
          }

          horariosEnviar.push({
            horario: indice + 1,
            dias: horarioEach.dias,
            servicios: [...new Set(servicios)],
            medicos: [...new Set(medicos)],
            manana: {
              disponible: horarioEach.manana[0].disponible,
              servicio: horarioEach.manana[0].servicio,
              medico: horarioEach.manana[0].medico,
              horas: mananaHoras,
            },
            tarde: {
              disponible: horarioEach.tarde[0].disponible,
              servicio: horarioEach.tarde[0].servicio,
              medico: horarioEach.tarde[0].medico,
              horas: tardeHoras,
            },
          });
        });

        // console.log(this.horarios);

        let info = { consultorio: consultorioInfo, horarios: horariosEnviar };
        // console.log(info);

        this.empresaService.postCrearConsultorio(info).subscribe(() => {
          this.formConsultorio.reset();
          document.getElementById("cerrar-modal-consultorio").click();
          this.getConsultorios();
        });
      })
      .catch(() => {
        if (this.horarios.length <= 1) {
          this.appService.swal(
            "Advertencia",
            "Por favor completa un horario de atención antes de crear un consultorio.",
            "warning"
          );
        }
      });
  }

  editarColsultorio(consultorio) {

    if (this.permisos.permisos(1, 1, 1, "editar")) {
      this.horarios = [];
      // this.horarios = consultorio.horarios;

      this.formConsultorio.setValue({
        nombre: consultorio.consultorio.nombreConsultorio,
        extencion: consultorio.consultorio.extensionConsultorio,
      });

      // console.log(consultorio);
      this.consultorio = consultorio.consultorio;
      this.estado = "editar";
      this.editHorario = "no_edit";
      document.getElementById("btn-consultorios").click();

      // this.setDias();
      // this.horarios = [];

      // // tslint:disable-next-line: prefer-for-of
      for (let i = 0; i < consultorio.horarios.length; i++) {
        let manana = [];
        let tarde = [];

        if (consultorio.horarios[i].horario[0].manana.disponible) {
          let desdeM = consultorio.horarios[
            i
          ].horario[0].manana.horas[0].hora.split(" ");
          // tslint:disable-next-line: max-line-length
          let hastaM = consultorio.horarios[i].horario[0].manana.horas[
            consultorio.horarios[i].horario[0].manana.horas.length - 1
          ].hora.split(" ");

          manana = [
            {
              disponible: true,
              desde: desdeM[0],
              hasta: hastaM[0],
              servicio: consultorio.horarios[i].horario[0].manana.servicio,
              medico: consultorio.horarios[i].horario[0].manana.medico,
              numeroCitas:
                consultorio.horarios[i].horario[0].manana.horas[0].numeroCitas,
              ok: true,
            },
          ];
        } else {
          manana = [
            {
              disponible: false,
              desde: null,
              hasta: null,
              servicio: null,
              medico: null,
              numeroCitas: null,
              ok: false,
            },
          ];
        }

        if (consultorio.horarios[i].horario[0].tarde.disponible) {
          let desdeT = consultorio.horarios[
            i
          ].horario[0].tarde.horas[0].hora.split(" ");
          // tslint:disable-next-line: max-line-length
          let hastaT = consultorio.horarios[i].horario[0].tarde.horas[
            consultorio.horarios[i].horario[0].tarde.horas.length - 1
          ].hora.split(" ");

          tarde = [
            {
              disponible: true,
              desde: desdeT[0],
              hasta: hastaT[0],
              servicio: consultorio.horarios[i].horario[0].tarde.servicio,
              medico: consultorio.horarios[i].horario[0].tarde.medico,
              numeroCitas:
                consultorio.horarios[i].horario[0].tarde.horas[0].numeroCitas,
              ok: true,
            },
          ];
        } else {
          tarde = [
            {
              disponible: false,
              desde: null,
              hasta: null,
              servicio: null,
              medico: null,
              numeroCitas: null,
              ok: false,
            },
          ];
        }

        this.horarios.push({
          horario: consultorio.horarios[i].horario[0].horario,
          dias: consultorio.horarios[i].horario[0].dias,
          manana,
          tarde,
          idHorario: consultorio.horarios[i]._id,
        });
      }

      // this.setDias();
      // // tslint:disable-next-line: prefer-for-of
      // for (let i = 0; i < this.horarios.length; i++) {
      //     this.disponibleDias(this.horarios[i].dias, false);
      // }

      // console.log(this.horarios);
      // console.log(document.getElementById('tarde0'));
    }
  }

  editarHorarioForm(posicion, horario) {
    // console.log(horario);

    let serviciosIniciales = [];

    if (horario.manana[0].disponible) {
      serviciosIniciales.push(horario.manana[0].servicio);
    }

    if (horario.tarde[0].disponible) {
      serviciosIniciales.push(horario.tarde[0].servicio);
    }

    serviciosIniciales = [...new Set(serviciosIniciales)];

    this.crearHorario = "no_crear";
    this.horario = horario;
    this.horario.serviciosIniciales = serviciosIniciales;
    this.horario.posicion = posicion;
    this.editFormEditarHorario(true);
    this.setDias();
    // tslint:disable-next-line: prefer-for-of
    for (let i = 0; i < this.horarios.length; i++) {
      if (posicion === i) {
        this.checkedDias(this.horarios[i].dias, true);
      } else {
        this.disponibleDias(this.horarios[i].dias, false);
      }
    }

    // console.log(this.horario);

    this.editHorario = "editar";
    location.hash = "#editConsultorio"; 
    // console.log(this.dias);
  }

  eliminarHorario(posicion, horario) {
    if (this.horarios.length <= 1) {
      this.appService.swal(
        "Advertencia",
        "No se puede eliminar el horario, el consultorio debe tener al menos un horario de atención.",
        "warning"
      );
      return;
    }

    let servicios = [];

    if (horario.manana[0].disponible) {
      servicios.push(horario.manana[0].servicio);
    }

    if (horario.tarde[0].disponible) {
      servicios.push(horario.tarde[0].servicio);
    }

    servicios = [...new Set(servicios)];

    let info = {
      idHorario: horario.idHorario,
      servicios,
      idSucursales: this.consultorio.idSucursales,
    };

    this.appService
      .swalConfirm(
        "Eliminar horario",
        "¿ Estas seguro que deseas eliminar el horario ?. Ten en cuenta que no se podra recupedad",
        "warning"
      )
      .then(() => {
        this.empresaService.dltHorario(info).subscribe(() => {
          this.horarios.splice(posicion, 1);
        });
      });
  }

  editFormEditarHorario(estado) {
    this.formEditHorario = new FormGroup({
      mananaDesde: new FormControl(),
      mananaHasta: new FormControl(),
      mananaServicio: new FormControl(),
      mananaMedico: new FormControl(),
      tardeDesde: new FormControl(),
      tardeHasta: new FormControl(),
      tardeServicio: new FormControl(),
      tardeMedico: new FormControl(),
    });

    if (estado) {
      this.formEditHorario.setValue({
        mananaDesde: this.horario.manana[0].desde,
        mananaHasta: this.horario.manana[0].hasta,
        mananaServicio: this.horario.manana[0].servicio,
        mananaMedico: this.horario.manana[0].medico,
        tardeDesde: this.horario.tarde[0].desde,
        tardeHasta: this.horario.tarde[0].hasta,
        tardeServicio: this.horario.tarde[0].servicio,
        tardeMedico: this.horario.tarde[0].medico,
      });
    }
  }

  editarHorario(bol) {
    // console.log(bol);

    this.agregarHorario(true, this.horario.posicion).then(() => {
      // console.log('todo bien', this.horario);

      var horariosEnviar = [];
      var mananaHoras = [];
      var tardeHoras = [];
      var servicios = [];
      var medicos = [];

      // console.log(this.horario);

      if (bol) {
        if (this.horario.manana[0].disponible) {
          servicios.push(this.horario.manana[0].servicio);
          medicos.push(this.horario.manana[0].medico);

          let diferenciaManana =
            1 +
            (parseInt(this.horario.manana[0].hasta) -
              parseInt(this.horario.manana[0].desde));

          for (let m = 0; m < diferenciaManana; m++) {
            let hora = parseInt(this.horario.manana[0].desde) + m;
            mananaHoras.push({
              disponible: true,
              hora: hora + " a.m",
              numeroCitas: this.horario.manana[0].numeroCitas,
            });
          }
          // console.log('manana', manana);
        }

        if (this.horario.tarde[0].disponible) {
          servicios.push(this.horario.tarde[0].servicio);
          medicos.push(this.horario.tarde[0].medico);

          let diferenciaTarde =
            1 +
            (parseInt(this.horario.tarde[0].hasta) -
              parseInt(this.horario.tarde[0].desde));

          for (let m = 0; m < diferenciaTarde; m++) {
            let hora = parseInt(this.horario.tarde[0].desde) + m;
            tardeHoras.push({
              disponible: true,
              hora: hora + " p.m",
              numeroCitas: this.horario.tarde[0].numeroCitas,
            });
          }
          // console.log('tarde', tarde);
        }

        horariosEnviar.push({
          horario: this.horario.horario,
          dias: this.horario.dias,
          serviciosIniciales: this.horario.serviciosIniciales,
          servicios: [...new Set(servicios)],
          medicos: [...new Set(medicos)],
          manana: {
            disponible: this.horario.manana[0].disponible,
            servicio: this.horario.manana[0].servicio,
            medico: this.horario.manana[0].medico,
            horas: mananaHoras,
          },
          tarde: {
            disponible: this.horario.tarde[0].disponible,
            servicio: this.horario.tarde[0].servicio,
            medico: this.horario.tarde[0].medico,
            horas: tardeHoras,
          },
        });

        let info = {
          idHorario: this.horario.idHorario,
          idSucursales: this.sucursal.idSucursales,
          idmunicipio: this.sucursal.idmunicipio,
          horario: horariosEnviar,
        };
        //  console.log(info);
        this.empresaService.putHorario(info).subscribe(() => {
          this.editHorario = "no_edit";
        });
      } else {
        // console.log(this.horarios);
        // console.log(this.horario);
        if (this.horarios[this.horarios.length - 1].manana[0].disponible) {
          servicios.push(
            this.horarios[this.horarios.length - 1].manana[0].servicio
          );
          medicos.push(
            this.horarios[this.horarios.length - 1].manana[0].medico
          );

          let diferenciaManana =
            1 +
            (parseInt(this.horarios[this.horarios.length - 1].manana[0].hasta) -
              parseInt(
                this.horarios[this.horarios.length - 1].manana[0].desde
              ));

          for (let m = 0; m < diferenciaManana; m++) {
            let hora =
              parseInt(
                this.horarios[this.horarios.length - 1].manana[0].desde
              ) + m;
            mananaHoras.push({
              disponible: true,
              hora: hora + " a.m",
              numeroCitas: this.horarios[this.horarios.length - 1].manana[0]
                .numeroCitas,
            });
          }
          // console.log('manana', manana);
        }

        if (this.horarios[this.horarios.length - 1].tarde[0].disponible) {
          servicios.push(
            this.horarios[this.horarios.length - 1].tarde[0].servicio
          );
          medicos.push(this.horarios[this.horarios.length - 1].tarde[0].medico);

          let diferenciaTarde =
            1 +
            (parseInt(this.horarios[this.horarios.length - 1].tarde[0].hasta) -
              parseInt(this.horarios[this.horarios.length - 1].tarde[0].desde));

          for (let m = 0; m < diferenciaTarde; m++) {
            let hora =
              parseInt(this.horarios[this.horarios.length - 1].tarde[0].desde) +
              m;
            tardeHoras.push({
              disponible: true,
              hora: hora + " p.m",
              numeroCitas: this.horarios[this.horarios.length - 1].tarde[0]
                .numeroCitas,
            });
          }
          // console.log('tarde', tarde);
        }

        horariosEnviar.push({
          horario: this.horarios.length - 1,
          dias: this.horarios[this.horarios.length - 1].dias,
          servicios: [...new Set(servicios)],
          medicos: [...new Set(medicos)],
          manana: {
            disponible: this.horarios[this.horarios.length - 1].manana[0]
              .disponible,
            servicio: this.horarios[this.horarios.length - 1].manana[0]
              .servicio,
            medico: this.horarios[this.horarios.length - 1].manana[0].medico,
            horas: mananaHoras,
          },
          tarde: {
            disponible: this.horarios[this.horarios.length - 1].tarde[0]
              .disponible,
            servicio: this.horarios[this.horarios.length - 1].tarde[0].servicio,
            medico: this.horarios[this.horarios.length - 1].tarde[0].medico,
            horas: tardeHoras,
          },
        });

        let info = {
          idmunicipio: this.sucursal.idmunicipio,
          idConsultorios: this.consultorio.idConsultorios,
          idSucursal: this.consultorio.idSucursales,
          horario: horariosEnviar,
        };
        // console.log(info);
        this.empresaService.postCrearHorarioConsultorio(info).subscribe(() => {
          this.editHorario = "no_edit";
        });
      }
    });
  }

  cambioHoraEdit(ev, hora, horario) {
    // console.log(this.horarios, horario);
    if (hora === "manana") {
      this.horario.manana[0].disponible = ev;
      this.horarios[horario - 1].manana[0].disponible = ev;
    }

    if (hora === "tarde") {
      this.horario.tarde[0].disponible = ev;
      this.horarios[horario - 1].tarde[0].disponible = ev;
    }
  }
}
